import axios from 'axios'
import qs from 'qs'
import deploy from './constant'
let ip = deploy.ip
axios.defaults.timeout = 120 * 1000; // 
axios.defaults.baseURL = ip // ip
let pending = [] //axios
var cancelToken = axios.CancelToken;
let removePending = (config) => {
	for (let i in pending) {
		if (pending[i].url === config.url) { //
			pending[i].f(); //
		}
	}
}

// 
axios.interceptors.request.use(
	config => {
		removePending(config)
		config.cancelToken = new cancelToken(function (cancel) {
			pending.push({
				url: config.url,
				f: cancel
			})
		})
		return Promise.resolve(config);
	},
	error => {
		// 
		return Promise.reject(error)
	}
)

// // 
axios.interceptors.response.use(
	response => {
		removePending(response.config); //axiospending
		// 
		return Promise.resolve(response)
	},
	error => {
		// 
		return Promise.reject(error)
	}
)

// 
export const indexs = (param) => {
	return axios.post(`new_app.php?c=article&a=index`,qs.stringify(param))
}

